import React, {useEffect} from 'react';
import { Steps } from 'antd';
import '../footer/styles/HowDoWeWork.css';
import circleIcon from '../../images/common/Circle.svg';
import $ from "jquery";

const { Step } = Steps;

export function HowDoWeWork() {
    useEffect(() => {
        const handleResize = () => {
            const element = ($ as any)('.ant-steps');
            if(window.innerWidth < 1300) {
                element.addClass('ant-steps-vertical');
                element.removeClass('ant-steps-horizontal');
                element.removeClass('ant-steps-label-vertical');
            } else {
                element.removeClass('ant-steps-vertical');
                element.addClass('ant-steps-horizontal');
                element.addClass('ant-steps-label-vertical');
            }
        };

        window.addEventListener('resize', handleResize);
        return () => {
            window.removeEventListener('resize', handleResize);
        };
    });

    if(window.innerWidth < 1300) {
        const element = ($ as any)('.ant-steps');

        element.addClass('ant-steps-vertical');
        element.removeClass('ant-steps-horizontal');
        element.removeClass('ant-steps-label-vertical');
    }

    return (
        <div>
          <p className='d-flex justify-content-center how-do-we-work-text'>  </p>
          <div className='d-flex justify-content-center col-11 mx-auto'>
              <Steps current={6} labelPlacement='vertical' direction={'horizontal'}>
                  <Step title=" 1" icon={<img src={circleIcon} title={'First step'} alt={'First step icon'}/>} subTitle=' ' description="        ,            ." />
                  <Step title=" 2" icon={<img src={circleIcon} title={'Second step'} alt={'Second step icon'}/>} subTitle=' ' description="                      ." />
                  <Step title=" 3" icon={<img src={circleIcon} title={'Third step'} alt={'Third step icon'}/>} subTitle=' ' description="                ." />
                  <Step title=" 4" icon={<img src={circleIcon} title={'Fourth step'} alt={'Fourth step icon'}/>} subTitle=' ' description="             ." />
                  <Step title=" 5" icon={<img src={circleIcon} title={'Fifth step'} alt={'Fifth step icon'}/>} subTitle=' ' description="             ." />
              </Steps>
          </div>
        </div>
  );
}
